.searchResult{
    display: flex;
    position: relative;
    margin: 20px;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    cursor: pointer;

}

.searchResult:hover{
    opacity: .8;

}

.searchResult > img{
    height: 200px;
    width: 350px;
    border-radius: 10px;
    /* overflow: hidden; */

}

.searchResult_info{
    display: flex;
    flex-direction:column;
    justify-content:space-between;
    padding-left: 20px;
}

.searchResult_heart{
    position: absolute;
    top:20px;
    right: 40px;
}

.searchResult_infoTop{
    width:40vw;
}
.searchResult_infoTop > h3{
    font-weight:400;
    margin-top:10px;
}
.searchResult_infoTop > p{
    margin-top:10px;
    font-size: 13px;
    color:grey;
}
.searchResult_star{
    color:red;
}

.searchResult_stars{
    display: flex;
    align-items: center;
}
.searchResult_price{
    position: absolute;
    bottom:20px;
    right:40px;
}
.searchResult_price > p{
    text-align: right;
}

